@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

.App {
  display: block;
  margin: 0 auto;
  text-align: center;
  width: 95%;
}
div .header {
    padding-top: 10px;
}

.icon {
    font-family: 'Material Icons', serif;
    font-style: normal;
    display: inline-block;
    vertical-align: middle;
    line-height: 1;
    text-transform: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-feature-settings: 'liga';
            font-feature-settings: 'liga';
}

.icon:hover {
    cursor: pointer;
}

.row {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
}

.row-middle {
    align-items: center;
}

.col {
    flex-grow: 1;
    flex-basis: 0;
    max-width: 100%;
}

.col-start {
    justify-content: flex-start;
    text-align: left;
}

.col-center {
    justify-content: center;
    text-align: center;
}

.col-end {
    justify-content: flex-end;
    text-align: right;
}

.search {
    outline: none;
    border: 1px solid #656565;
    border-radius: 3px;
}

.search:hover,:focus {
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 255, 0.13);
}

.calendar {
    display: block;
    position: relative;
    width: 100%;
    background: #f5ffdb;
    border: 1px solid #eee;
}

.calendar .days {
    text-transform: uppercase;
    font-weight: 400;
    color: #494949;
    font-size: 70%;
    padding: .75em 0;
    border-bottom: 1px solid #eee;
}

.calendar .body .cell {
    position: relative;
    height: 5em;
    border-right: 1px solid #eee;
    overflow: hidden;
    cursor: pointer;
    background: #fff;
    -webkit-transition: 0.25s ease-out;
    transition: 0.25s ease-out;
}

.calendar .body .cell:hover {
    background: #f5ffdb;
    -webkit-transition: 0.5s ease-out;
    transition: 0.5s ease-out;
}

.calendar .body .selected {
    border: 1px solid #2c6fee;
}

.calendar .body .today {
    background: rgba(114, 187, 255, 0.4);
}

.btn {
    background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #33bdef), to(#019ad2));
    background:-webkit-linear-gradient(top, #33bdef 5%, #019ad2 100%);
    background:linear-gradient(to bottom, #33bdef 5%, #019ad2 100%);
    border-radius:4px;
    display:inline-block;
    cursor:pointer;
    color:#ffffff;
    font-size:14px;
    padding:2px 10px;
    text-decoration:none;
    margin-top: 3px;
}

.update {
    margin-left: 5px;
}

.btn:hover {
    background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #019ad2), to(#33bdef));
    background:-webkit-linear-gradient(top, #019ad2 5%, #33bdef 100%);
    background:linear-gradient(to bottom, #019ad2 5%, #33bdef 100%);
}
.btn:active {
    position:relative;
}

a .btn:visited {
    text-decoration: none;
}

a .btn:active {
    text-decoration: none;
}

.calendar .body .row {
    border-bottom: 1px solid #eee;
}

.calendar .body .row:last-child {
    border-bottom: none;
}

.calendar .body .cell:last-child {
    border-right: none;
}

.calendar .body .cell .number {
    position: absolute;
    font-size: 82.5%;
    line-height: 1;
    top: .75em;
    right: .75em;
    font-weight: 700;
}

.calendar .body .disabled {
    color: #ccc;
    pointer-events: none;
}

.calendar .body .cell .bg {
    font-weight: 700;
    line-height: 1;
    color: #1a8fff;
    opacity: 0;
    font-size: 8em;
    position: absolute;
    top: -.2em;
    right: -.05em;
    -webkit-transition: .25s ease-out;
    transition: .25s ease-out;
    letter-spacing: -.07em;
}

.calendar .body .cell:hover .bg, .calendar .body .selected .bg  {
    opacity: 0.05;
    -webkit-transition: .5s ease-in;
    transition: .5s ease-in;
}

.calendar .body .col {
    flex-grow: 0;
    flex-basis: calc(100%/7);
    width: calc(100%/7);
}

.popupForm {
    padding: 10px 10px 10px 10px;
    margin: 0 auto;
}

.popup_input {
    width: 100%;
    outline: none;
    border: 1px solid #656565;
    border-radius: 3px;
}

.popup_btn {
    float: left;
    margin-bottom: 20px;
}

.popup_description {
    float: left;
    font-size: 14px;
}

.btn_done {
    margin-right: 15px;
}

.btn_done, .btn_delete {
    background: rgba(66, 184, 221, 0.33);
    border: 1px solid #000000;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
    border-radius: 3px;
}

.btn_done,
.btn_delete:hover {
    cursor: pointer;
}

